import { useState, useRef, useCallback, useEffect } from 'react';

interface AudioRecorderProps {
  onRecordingComplete?: (blob: Blob) => void;
  onRecordingStart?: () => void;
  onRecordingStop?: () => void;
  onVolumeChange?: (volume: number) => void;
  disabled?: boolean;
}

export default function AudioRecorder({ 
  onRecordingComplete,
  onRecordingStart,
  onRecordingStop,
  onVolumeChange,
  disabled = false
}: AudioRecorderProps) {
  const [isRecording, setIsRecording] = useState(false);
  const [volume, setVolume] = useState(0);
  const [recordingTime, setRecordingTime] = useState(0);
  
  const mediaRecorderRef = useRef<MediaRecorder | null>(null);
  const audioContextRef = useRef<AudioContext | null>(null);
  const analyserRef = useRef<AnalyserNode | null>(null);
  const sourceRef = useRef<MediaStreamAudioSourceNode | null>(null);
  const animationFrameRef = useRef<number | null>(null);
  const audioChunksRef = useRef<Blob[]>([]);
  const timerRef = useRef<ReturnType<typeof setInterval> | null>(null);

  // 
  const cleanup = useCallback(() => {
    if (animationFrameRef.current) {
      cancelAnimationFrame(animationFrameRef.current);
      animationFrameRef.current = null;
    }
    if (timerRef.current) {
      clearInterval(timerRef.current);
      timerRef.current = null;
    }
    if (sourceRef.current) {
      sourceRef.current.disconnect();
      sourceRef.current = null;
    }
    if (analyserRef.current) {
      analyserRef.current.disconnect();
      analyserRef.current = null;
    }
    if (audioContextRef.current && audioContextRef.current.state !== 'closed') {
      audioContextRef.current.close();
      audioContextRef.current = null;
    }
    if (mediaRecorderRef.current) {
      mediaRecorderRef.current.stream.getTracks().forEach(track => track.stop());
      mediaRecorderRef.current = null;
    }
  }, []);

  useEffect(() => {
    return cleanup;
  }, [cleanup]);

  // 
  const startRecording = useCallback(async () => {
    if (disabled) return;
    
    try {
      //  - 
      const stream = await navigator.mediaDevices.getUserMedia({ 
        audio: {
          echoCancellation: false,
          noiseSuppression: false,
          autoGainControl: false,
        }
      });
      
      //  AudioContext -  iOS Safari
      const AudioContextClass = window.AudioContext || (window as unknown as { webkitAudioContext: typeof AudioContext }).webkitAudioContext;
      const audioContext = new AudioContextClass();
      audioContextRef.current = audioContext;
      
      //  iOS AudioContext
      if (audioContext.state === 'suspended') {
        await audioContext.resume();
      }
      
      //  AnalyserNode 
      const analyser = audioContext.createAnalyser();
      analyser.fftSize = 256;
      analyserRef.current = analyser;
      
      //  analyser
      const source = audioContext.createMediaStreamSource(stream);
      source.connect(analyser);
      sourceRef.current = source;
      
      //  MediaRecorder -  MIME 
      const mimeType = MediaRecorder.isTypeSupported('audio/webm') 
        ? 'audio/webm' 
        : MediaRecorder.isTypeSupported('audio/mp4') 
          ? 'audio/mp4' 
          : 'audio/ogg';
      
      const mediaRecorder = new MediaRecorder(stream, { mimeType });
      mediaRecorderRef.current = mediaRecorder;
      audioChunksRef.current = [];
      
      mediaRecorder.ondataavailable = (event) => {
        if (event.data.size > 0) {
          audioChunksRef.current.push(event.data);
        }
      };
      
      mediaRecorder.onstop = () => {
        const audioBlob = new Blob(audioChunksRef.current, { type: mimeType });
        onRecordingComplete?.(audioBlob);
      };
      
      // 
      mediaRecorder.start(100); //  100ms 
      setIsRecording(true);
      setRecordingTime(0);
      onRecordingStart?.();
      
      // 
      timerRef.current = setInterval(() => {
        setRecordingTime(prev => prev + 1);
      }, 1000);
      
      // 
      const dataArray = new Uint8Array(analyser.frequencyBinCount);
      
      const updateVolume = () => {
        if (!analyserRef.current) return;
        
        analyserRef.current.getByteFrequencyData(dataArray);
        
        // 
        let sum = 0;
        for (let i = 0; i < dataArray.length; i++) {
          sum += dataArray[i];
        }
        const average = sum / dataArray.length;
        const normalizedVolume = Math.min(average / 128, 1); //  0-1
        
        setVolume(normalizedVolume);
        onVolumeChange?.(normalizedVolume);
        animationFrameRef.current = requestAnimationFrame(updateVolume);
      };
      
      updateVolume();
      
    } catch (error) {
      console.error(':', error);
      // 
      let errorMsg = '';
      if (error instanceof DOMException) {
        if (error.name === 'NotAllowedError') {
          errorMsg = '';
        } else if (error.name === 'NotFoundError') {
          errorMsg = '';
        }
      }
      alert(errorMsg);
    }
  }, [disabled, onRecordingComplete, onRecordingStart, onVolumeChange]);

  // 
  const stopRecording = useCallback(() => {
    if (mediaRecorderRef.current && mediaRecorderRef.current.state !== 'inactive') {
      mediaRecorderRef.current.stop();
    }
    cleanup();
    setIsRecording(false);
    setVolume(0);
    onVolumeChange?.(0);
    onRecordingStop?.();
  }, [cleanup, onRecordingStop, onVolumeChange]);

  // 
  const formatTime = (seconds: number) => {
    const mins = Math.floor(seconds / 60);
    const secs = seconds % 60;
    return `${mins.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`;
  };

  // 
  const getVolumeColor = () => {
    if (volume < 0.3) return '#4ade80'; //  - 
    if (volume < 0.6) return '#fbbf24'; //  - 
    return '#3b82f6'; //  - 
  };

  return (
    <div style={{
      ...styles.container,
      opacity: disabled ? 0.5 : 1,
      pointerEvents: disabled ? 'none' : 'auto',
    }}>
      {/*  */}
      <div style={styles.timeDisplay}>
        {formatTime(recordingTime)}
      </div>
      
      {/*  */}
      <div style={styles.visualizerContainer}>
        <div style={styles.volumeBars}>
          {Array.from({ length: 20 }).map((_, i) => {
            const threshold = (i + 1) / 20;
            const isActive = volume >= threshold;
            return (
              <div
                key={i}
                style={{
                  ...styles.volumeBar,
                  backgroundColor: isActive ? getVolumeColor() : '#374151',
                  height: `${Math.max(8, (i + 1) * 4)}px`,
                }}
              />
            );
          })}
        </div>
        <div style={styles.volumeText}>
          : {Math.round(volume * 100)}%
        </div>
      </div>
      
      {/*  */}
      <div style={styles.controls}>
        {!isRecording ? (
          <button
            onClick={startRecording}
            style={{ ...styles.button, ...styles.startButton }}
          >
            <span style={styles.buttonIcon}></span>
            
          </button>
        ) : (
          <button
            onClick={stopRecording}
            style={{ ...styles.button, ...styles.stopButton }}
          >
            <span style={styles.buttonIcon}></span>
            
          </button>
        )}
      </div>
      
      {/*  */}
      <div style={styles.status}>
        {isRecording ? (
          <span style={styles.recordingIndicator}>
            <span style={styles.recordingDot} /> ...
          </span>
        ) : (
          <span style={styles.idleStatus}></span>
        )}
      </div>
    </div>
  );
}

const styles: Record<string, React.CSSProperties> = {
  container: {
    padding: '1.5rem',
    backgroundColor: '#1a1a2e',
    borderRadius: '12px',
    border: '1px solid #333355',
    transition: 'opacity 0.2s',
    touchAction: 'manipulation', // 
    WebkitTapHighlightColor: 'transparent', // 
  },
  timeDisplay: {
    fontSize: 'clamp(1.75rem, 8vw, 2.5rem)', // 
    fontWeight: 'bold',
    color: '#fff',
    textAlign: 'center',
    fontFamily: 'monospace',
    marginBottom: '1rem',
    textShadow: '0 0 20px rgba(102, 126, 234, 0.5)',
  },
  visualizerContainer: {
    marginBottom: '1.25rem',
  },
  volumeBars: {
    display: 'flex',
    alignItems: 'flex-end',
    justifyContent: 'center',
    gap: 'clamp(2px, 1vw, 4px)', // 
    height: 'clamp(60px, 15vw, 80px)', // 
    padding: '10px',
    backgroundColor: '#0a0a0f',
    borderRadius: '8px',
    border: '1px solid #222244',
  },
  volumeBar: {
    width: 'clamp(4px, 2vw, 8px)', // 
    borderRadius: '2px',
    transition: 'all 0.05s ease',
  },
  volumeText: {
    textAlign: 'center',
    color: '#8888aa',
    fontSize: 'clamp(0.75rem, 3vw, 0.875rem)', // 
    marginTop: '0.5rem',
  },
  controls: {
    display: 'flex',
    justifyContent: 'center',
    marginBottom: '1rem',
  },
  button: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    gap: '8px',
    width: '100%',
    padding: 'clamp(0.875rem, 4vw, 1rem) clamp(1rem, 5vw, 1.5rem)', // 
    fontSize: 'clamp(0.9rem, 4vw, 1rem)', // 
    fontWeight: '600',
    border: 'none',
    borderRadius: '8px',
    cursor: 'pointer',
    transition: 'all 0.2s ease',
    WebkitTouchCallout: 'none', // 
    userSelect: 'none',
  },
  startButton: {
    background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
    color: 'white',
  },
  stopButton: {
    background: 'linear-gradient(135deg, #f87171 0%, #dc2626 100%)',
    color: 'white',
  },
  buttonIcon: {
    fontSize: 'clamp(1rem, 4vw, 1.2rem)',
  },
  status: {
    textAlign: 'center',
    fontSize: 'clamp(0.8rem, 3.5vw, 0.875rem)',
  },
  recordingIndicator: {
    display: 'inline-flex',
    alignItems: 'center',
    gap: '6px',
    color: '#f87171',
  },
  recordingDot: {
    width: '8px',
    height: '8px',
    backgroundColor: '#f87171',
    borderRadius: '50%',
    animation: 'pulse 1s infinite',
  },
  idleStatus: {
    color: '#666688',
  },
};
